import json from '@/json';
import mixLoading from '@/components/mix-loading/mix-loading';
import request from '@/common/requesst/interface.js'
import uniPagination from '@/components/uni-pagination/uni-pagination.vue'
import {getImgUrl} from '@/common/fileUtil.js'
export default {
	components: {
		mixLoading,
		uniPagination
	},
	data() {
		return {
			loading: true,
			postId: null,
			detailData: {},
			newsList: [],
			evaList: [],
			article: {},
			comments: {
				startPage: 1,
				pageSize: 5,
				totalPage: 1,
				list:[]
			},
			newComment: ''
		}
	},
	onLoad(options){
		this.postId = options.data
		console.log(this.postId)
		
		this.loadNewsList();
		this.loadEvaList();
	},
	methods: {
		//获取推荐列表
		loadNewsList(){
			request({
				url: '/posts/' + this.postId,
				method: 'GET'
			}).then( res => {
				if ( res.data.code === 1000) {
					this.article = res.data.data
					this.loading = false
				}
			})
		},
		//获取评论列表
		loadEvaList(){
			this.comments.data = {
				postId: this.postId
			}
			request({
				url: '/comment/getList',
				method: 'POST',
				data: {
					startPage: this.comments.startPage,
					pageSize: this.comments.pageSize,
					data: {
						postId: this.postId
					}
				}
			}).then( res => {
				if (res.data.code === 1000) {
					this.comments = res.data.data
					this.comments.list.forEach( item => {
						item.userInfo.personalImage = getImgUrl(item.userInfo.personalImage)
					})
					console.log('评论加载成功')
				}
			})
		},
		handleCurrentChange: function(e) {
			this.comments.startPage = e.current
			this.loadEvaList()
		},
		handleCommentSubmit: function() {
			if (!this.newComment) {
				uni.showModal({
					showCancel: false,
					title: '提示',
					content: '请输入内容后再提交'
				})
			} else {
				request({
					url: '/comment',
					method: 'POST',
					data: {
						commentContent: this.newComment,
						postId: this.postId
					}
				}).then( res => {
					if (res.data.code === 1000) {
						uni.showToast({
							title:'评论成功'
						})
						this.newComment = ''
						
						this.loadEvaList()
					}
				})
			}
		}
	}
}
